<template>
  <div class="section">
    <div id="line"></div>
  </div>
</template>

<script>
import echarts from 'echarts'
export default {
  data () {
    return {
    }
  },

  components: {},

  computed: {},

  mounted: function() {
    this.line()
  },

  methods: {
    line() {
        let line = echarts.init(document.getElementById('line'));
        line.setOption({
            color: ['#3398DB'],
            tooltip : {
                trigger: 'axis',
                axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                    type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                }
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            xAxis : [
                {
                    type : 'category',
                    data : ['移井架/搬迁', '油套管问题', '拆/装BOP及试压', '接钻具', '井控复杂', '井控复杂', '钻套管附件和水泥塞','移井架/搬迁', '油套管问题', '拆/装BOP及试压', '接钻具', '井控复杂', '井控复杂', '钻套管附件和水泥塞'],
                }
            ],
            yAxis : [
                {
                    type : 'value'
                }
            ],
            dataZoom: [
                {
                    type: 'inside'
                }
            ],
            series : [
                {
                    name:'直接访问',
                    type:'bar',
                    barWidth: '60%',
                    label: {
                        normal: {
                            show: true,
                            position: 'top',
                            color: '#f0f'
                        }
                    },
                    data:[380, 350, 340, 334, 310, 300, 220,210, 202, 200, 134, 120, 110, 100]
                },
                {
                    name:'直接访问',
                    type:'line',
                    lineStyle: {
                        color: '#f00'
                    },
                    itemStyle: {
                        color: '#eee',
                        borderWidth: 5
                    },
                    emphasis: {
                        itemStyle: {
                            borderWidth: 0
                        }
                    },
                    data:[380, 350, 340, 334, 310, 300, 220,210, 202, 200, 134, 120, 110, 100]
                }
            ]
        })
    },
  }
}
</script>

<style lang='scss' scoped>
#line {
  height: 100%;
  background: #fff;
}
</style>



